footer {
	position: absolute;
	right: 0;
	bottom: 0;
	left: 0;
	padding: 1rem;
	border: 1px solid #eeeeee;
	color: #757575;
	background-color: white;
	text-align: center;
	display: grid;
	grid-template: 1fr / 1fr 1fr 1fr;
}

footer .github a {
	float: right;
}

footer .author {
	font-size: 0.9em;
}


